@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("./../fonts/pt-mono-regular.woff2") format("woff2"), url("./../fonts/pt-mono-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("./../fonts/roboto-condensed-regular.woff2") format("woff2"), url("./../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("./../fonts/roboto-condensed-bold.woff2") format("woff2"), url("./../fonts/roboto-condensed-bold.woff") format("woff");
}
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

h2 {
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
}
@media (max-width: 1023px) {
  h2 {
    font-size: 32px;
    line-height: 140.62%;
  }
}
@media (max-width: 767px) {
  h2 {
    font-size: 28px;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: inherit;
}

.content {
  flex: 1 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

@media (max-width: 767px) {
  .logo-light {
    display: none;
  }
}

.logo-mobile {
  display: none;
}
@media (max-width: 767px) {
  .logo-mobile {
    display: block;
  }
}

.container {
  max-width: 1410px;
  padding-left: 25px;
  padding-right: 25px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1023px) {
  .container {
    padding-left: 25px;
    padding-right: 25px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  z-index: 5;
}
@media (max-width: 1023px) {
  .page-header {
    left: auto;
    transform: none;
    width: 100%;
  }
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1461px;
  padding-top: 33px;
  padding-bottom: 33px;
}
@media (max-width: 1023px) {
  .page-header__container {
    padding-top: 20px;
    padding-bottom: 27px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    padding-top: 14px;
    padding-bottom: 17px;
  }
}

.page-header a {
  display: flex;
  align-items: center;
}

.page-header__contacts {
  display: grid;
  grid-template-columns: 425px 180px;
  align-items: center;
  column-gap: 49px;
  width: 654px;
}
@media (max-width: 1023px) {
  .page-header__contacts {
    grid-template-columns: 1fr;
    width: auto;
  }
}

.page-header button {
  padding: 14px 13px 15px;
  width: 180px;
  min-height: 45px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: inherit;
  border: 1px solid #ffffff;
  border-radius: 5px;
}
.page-header button:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 1023px) {
  .page-header button {
    display: none;
  }
}

.contacts-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  width: 440px;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .contacts-list {
    width: 84px;
  }
}
@media (max-width: 767px) {
  .contacts-list {
    width: 73px;
  }
}

@media (max-width: 1023px) {
  .contacts-list li:first-child {
    margin-right: 35px;
  }
}
@media (max-width: 767px) {
  .contacts-list li:first-child {
    margin-right: 24px;
  }
}

.contacts-list a {
  display: flex;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 10px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
}
.contacts-list a:hover {
  color: rgba(255, 255, 255, 0.8);
}

@media (max-width: 1023px) {
  .contacts-list__svg-email {
    width: 25px;
    height: 19px;
  }
}

@media (max-width: 1023px) {
  .contacts-list__svg-phone {
    width: 24px;
    height: 24px;
  }
}

.contacts-list span {
  margin-left: 13px;
}
@media (max-width: 1023px) {
  .contacts-list span {
    display: none;
  }
}

.promo {
  background-color: #1b2d37;
  background-image: url("../img/main-background.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.promo__container {
  position: relative;
  max-width: 1490px;
  padding: 316px 33px 275px;
}
@media (max-width: 1023px) {
  .promo__container {
    padding: 188px 200px 163px 88px;
  }
}
@media (max-width: 767px) {
  .promo__container {
    padding: 286px 15px 70px;
  }
}

.promo h1 {
  position: relative;
  width: 65%;
  margin-top: 0;
  margin-bottom: 25px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff;
  z-index: 1;
}
@media (max-width: 1023px) {
  .promo h1 {
    margin-bottom: 29px;
    font-size: 60px;
    line-height: 70px;
  }
}
@media (max-width: 767px) {
