    font-size: 40px;
    line-height: 47px;
  }
}

.promo p {
  position: relative;
  width: 50%;
  padding-left: 99px;
  margin-top: 0;
  margin-bottom: 44px;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff;
  z-index: 1;
}
.promo p::after {
  position: absolute;
  top: 16px;
  left: 4px;
  content: "";
  width: 66px;
  height: 2px;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .promo p {
    width: auto;
    padding-left: 51px;
    margin-bottom: 55px;
    font-size: 18px;
    line-height: 156.8%;
  }
  .promo p::after {
    width: 33px;
  }
}
@media (max-width: 767px) {
  .promo p {
    margin-bottom: 41px;
    font-size: 16px;
  }
}

.promo a {
  display: inline-block;
  max-width: 407px;
  margin-left: 99px;
  padding: 17px 45px 20px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  line-height: 19px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #00b2ff;
  border-radius: 5px;
}
.promo a:hover {
  background-color: rgba(0, 178, 255, 0.5);
}
@media (max-width: 1023px) {
  .promo a {
    max-width: 379px;
    padding: 17px 31px 19px;
    margin-left: 51px;
  }
}
@media (max-width: 767px) {
  .promo a {
    width: 100%;
    padding-left: 35px;
    padding-right: 30px;
    margin-left: 0;
  }
}

.promo span:nth-child(3n-1) {
  display: none;
}
@media (max-width: 767px) {
  .promo span:nth-child(3n-1) {
    display: block;
  }
}

@media (max-width: 767px) {
  .promo span:nth-child(2n-1) {
    display: none;
  }
}

.promo__picture {
  position: absolute;
  top: 116px;
  left: 100%;
  margin-left: -45%;
  height: 655px;
  width: 48%;
  z-index: 0;
}
@media (max-width: 1023px) {
  .promo__picture {
    left: auto;
    right: 0;
    top: 0;
    margin-left: 0;
    width: 330px;
    height: 466px;
  }
}
@media (max-width: 767px) {
  .promo__picture {
    width: 294px;
    height: 437px;
    right: 0;
    top: 0;
  }
}

.advantage {
  position: relative;
  overflow: hidden;
}
.advantage::before {
  position: absolute;
  top: 14px;
  content: "";
  width: 100%;
  height: 1260px;
  background-image: url("../img/triangles-advantage.png");
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1;
}
@media (max-width: 1023px) {
  .advantage::before {
    top: 207px;
    right: -38px;
    width: 636px;
    height: 640px;
    background-image: url("../img/triangles-advantage-tablet.png");
  }
}
@media (max-width: 767px) {
  .advantage::before {
    top: inherit;
    bottom: 387px;
    right: -157px;
  }
}

.advantage__container {
  padding-top: 112px;
}
@media (max-width: 1023px) {
  .advantage__container {
    padding-top: 54px;
  }
}
@media (max-width: 767px) {
  .advantage__container {
    padding-top: 37px;
    padding-left: 14px;
  }
}

.advantage ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 29px;
  row-gap: 30px;
  max-width: 1170px;
  padding: 0;
  margin: 0 auto;
  list-style: none;
}
@media (max-width: 767px) {
  .advantage ul {
    flex-direction: column;
    row-gap: 20px;
  }
}

.advantage li {
  min-height: 299px;
  width: calc(50% - 15px);
  padding: 40px 35px 49px 41px;
  background-color: #ffffff;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;
}
@media (max-width: 1023px) {
  .advantage li {
    min-height: 232px;
    padding: 24px 18px 21px 25px;
  }
}
@media (max-width: 767px) {
  .advantage li {
    width: 100%;
