  }
}

@media (max-width: 1023px) {
  .advantage li:nth-child(4n-1) h3 {
    padding-top: 7px;
    margin-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .advantage li:nth-child(4n-1) h3 {
    padding-top: 10px;
    margin-bottom: 17px;
  }
}

@media (max-width: 1023px) {
  .advantage li:nth-child(5n-1) h3 {
    padding-top: 4px;
    margin-bottom: 4px;
  }
}
@media (max-width: 767px) {
  .advantage li:nth-child(5n-1) h3 {
    margin-bottom: 15px;
  }
}

@media (max-width: 767px) {
  .advantage li:nth-child(3n-1) {
    padding-right: 0;
  }
}

@media (max-width: 767px) {
  .advantage li:nth-child(4n-1) {
    min-height: 232px;
  }
}

@media (max-width: 767px) {
  .advantage li:nth-child(5n-1) {
    min-height: 244px;
  }
}

.advantage li:hover {
  background-color: #00b2ff;
}
.advantage li:hover h3,
.advantage li:hover p {
  color: #ffffff;
}
.advantage li:hover svg {
  fill: #ffffff;
}

.advantage li:active,
.advantage li:focus {
