}

.advantage h3 {
  padding-top: 17px;
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantage h3 {
    padding-top: 4px;
    margin-bottom: 7px;
  }
}
@media (max-width: 767px) {
  .advantage h3 {
    padding-top: 10px;
    margin-bottom: 8px;
    font-size: 20px;
    line-height: 140.62%;
  }
}

.advantage p {
  margin: 0;
  line-height: 22px;
  color: #666666;
}

.advantage__list p {
  white-space: break-spaces;
}

.advantage svg {
  fill: #00b2ff;
}

@media (max-width: 1023px) {
  .advantage__hired-svg {
    width: 32px;
    height: 32px;
  }
}

@media (max-width: 1023px) {
  .advantage__detective-svg {
    width: 30px;
    height: 26px;
  }
}

